@import "react-big-calendar/lib/css/react-big-calendar.css";

html {
  --primary-color: #fff7f7;
  --sec-color: #000000ab;
  --thr-color: #0c0c0c;
  --backColor: #e6e6e6;
  --bordColor: #adadad;
  --bordColor-table: #d6d3d3b2;
  --bordColor-scroll: #252525;
}

html[data-theme="dark"] {
  --primary-color: #1c1c1c;
  --sec-color: #05c4ddab;
  --thr-color: #ffffff;
  --backColor: #05a4b9e0;
  --bordColor: #f5efef;
  --bordColor-table: #252525;
  --bordColor-scroll: #252525;
}

.p-login {
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.modal {
  background: var(--primary-color);
  color: var(--sec-color);
}

.modal::-webkit-scrollbar {
  width: 12px; /* width of the entire scrollbar */
}

.modal::-webkit-scrollbar:vertical {
  width: 10px;
}

.modal::-webkit-scrollbar-button:increment,
.modal::-webkit-scrollbar-button {
  display: none;
}

.modal::-webkit-scrollbar:horizontal {
  height: 10px;
}

.modal::-webkit-scrollbar-track {
  background: var(--primary-color); /* color of the tracking area */
}

.modal::-webkit-scrollbar-thumb {
  background-color: var(--sec-color); /* color of the scroll thumb */
  border-radius: 20px; /* roundness of the scroll thumb */
  border: 2px solid var(--bordColor-scroll); /* creates padding around scroll thumb */
}

.rbc-date-cell {
  color: var(--sec-color);
}

.rbc-toolbar {
  background-color: var(--primary-color);
  color: var(--thr-color);
  margin-top: 7px;
}
.rbc-header {
  background-color: var(--primary-color);
  color: var(--thr-color);
}

.rbc-btn-group {
  background-color: var(--primary-color);
}

.rbc-label {
  background-color: var(--primary-color);
  color: var(--thr-color);
}

.rbc-calendar {
  background-color: var(--primary-color);
}

.rbc-time-content::-webkit-scrollbar {
  width: 12px; /* width of the entire scrollbar */
}

.rbc-time-content::-webkit-scrollbar:vertical {
  width: 9px;
}

.rbc-time-content::-webkit-scrollbar-button:increment,
.rbc-time-content::-webkit-scrollbar-button {
  display: none;
}

.rbc-time-content::-webkit-scrollbar:horizontal {
  height: 10px;
}

.rbc-time-content::-webkit-scrollbar-track {
  background: var(--primary-color); /* color of the tracking area */
}

.rbc-time-content::-webkit-scrollbar-thumb {
  background-color: var(--sec-color); /* color of the scroll thumb */
  border-radius: 20px; /* roundness of the scroll thumb */
  border: 2px solid var(--bordColor-scroll); /* creates padding around scroll thumb */
}

.rbc-toolbar button {
  color: var(--sec-color);
  display: inline-block;
  margin: 0;
  text-align: center;
  vertical-align: middle;
  background: none;
  background-image: none;
  border: 1px solid var(--bordColor);
  padding: 0.375rem 1rem;
  border-radius: 4px;
  line-height: normal;
  white-space: nowrap;
}

.rbc-toolbar button:active,
.rbc-toolbar button.rbc-active {
  background-image: none;
  box-shadow: var(--primary-color);
  background-color: var(--primary-color);
  border-color: var(--bordColor);
}

.rbc-toolbar button:active:hover,
.rbc-toolbar button:active:focus,
.rbc-toolbar button.rbc-active:hover,
.rbc-toolbar button.rbc-active:focus {
  color: var(--thr-color);
  background-color: var(--backColor);
  border-color: var(--bordColor);
}

.rbc-off-range-bg {
  background: var(--primary-color);
}
.rbc-today {
  background-color: var(--primary-color);
}

.rbc-toolbar button:hover {
  color: #373a3c;
  background-color: var(--backColor);
  border-color: var(--bordColor);
}

.rbc-toolbar button:focus {
  color: var(--primary-color);
  background-color: var(--backColor);
  border-color: var(--bordColor);
}
.rbc-day-bg + .rbc-day-bg {
  border-left: 1px solid var(--bordColor-table);
}

.rbc-time-content > * + * > * {
  border-left: none;
  border-bottom: none;
}

.rbc-day-slot .rbc-time-slot {
  border-top: none;
}

.rbc-header {
  overflow: hidden;
  flex: 1 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 0 3px;
  text-align: center;
  vertical-align: middle;
  font-weight: bold;
  font-size: 90%;
  min-height: 0;
  border-bottom: 1px solid var(--bordColor-table);
}

.rbc-timeslot-group {
  border-bottom: 1px solid var(--bordColor-table);
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: var(--bordColor-table);
  min-height: 40px;
  display: flex;
  flex-flow: column nowrap;
}

.rbc-header + .rbc-header {
  border-left: 1px solid var(--bordColor-table);
}

.rbc-time-view .rbc-row {
  box-sizing: border-box;
  min-height: auto;
}

body::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 12px; /* width of the entire scrollbar */
}

body::-webkit-scrollbar:vertical {
  width: 10px;
}

body::-webkit-scrollbar-button:increment,
body::-webkit-scrollbar-button {
  display: none;
}

body::-webkit-scrollbar:horizontal {
  height: 10px;
}

body::-webkit-scrollbar-track {
  background: var(--primary-color); /* color of the tracking area */
}

body::-webkit-scrollbar-thumb {
  background-color: var(--sec-color); /* color of the scroll thumb */
  border-radius: 20px; /* roundness of the scroll thumb */
  border: 2px solid var(--bordColor-scroll); /* creates padding around scroll thumb */
}
